<template>
  <div class="publish-container">
    <el-card>
      <div slot="header">
        <my-bread>{{isXiu?"":""}}</my-bread>
      </div>
      <el-form label-width="100px" v-model="queryFormData">
        <el-form-item label="">
          <el-input style="width:400px" v-model="queryFormData.title"></el-input>
        </el-form-item>
        <el-form-item label="">
          <quill-editor v-model="queryFormData.content" :options="editorOption"></quill-editor>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="queryFormData.cover.type" @change="typeChange">
            <el-radio :label="1"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="-1"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="queryFormData.cover.type===1">
          <!--  -->
          <my-image v-model="queryFormData.cover.images[0]"></my-image>
        </el-form-item>
        <el-form-item v-if="queryFormData.cover.type===3" class="type3">
          <!--  -->
          <my-image v-model="queryFormData.cover.images[0]"></my-image>
          <my-image v-model="queryFormData.cover.images[1]"></my-image>
          <my-image v-model="queryFormData.cover.images[2]"></my-image>
        </el-form-item>
        <el-form-item label="">
          <my-channel v-model="queryFormData.channel_id"></my-channel>
        </el-form-item>
        <el-form-item v-if="isXiu">
          <el-button type="primary" @click="putPublished(false)"></el-button>
          <el-button type="info" @click="putPublished(true)"></el-button>
        </el-form-item>
        <el-form-item v-else>
          <el-button type="primary" @click="uploadPublished(false)"></el-button>
          <el-button type="info" @click="uploadPublished(true)"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
// require styles
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

import { quillEditor } from 'vue-quill-editor'

export default {
  components: {
    quillEditor
  },
  data () {
    return {
      queryFormData: {
        // draft: false, // true  false,      //true 
        title: '', // string
        content: '', // string X
        cover: {
          type: 1, // integer -1:0 :1 :3 :
          images: [] // string []item : string X

        },
        channel_id: null // integerid
      },
      // 
      editorOption: {
        placeholder: '',
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'],
            ['blockquote', 'code-block'],
            [{ 'header': 1 }, { 'header': 2 }],
            [{ 'list': 'ordered' }, { 'list': 'bullet' }],
            [{ 'indent': '-1' }, { 'indent': '+1' }]
          ]
        }
      },
      articleId: null, // id
      isXiu: false // true/false 

    }
  },
  watch: {
    $route (data) {
      this.articleId = null
      this.isXiu = false
      this.queryFormData = {
        // draft: false, // true  false,      //true 
        title: '', // string
        content: '', // string X
        cover: {
          type: 1, // integer -1:0 :1 :3 :
          images: [] // string []item : string X

        },
        channel_id: null // integerid
      }
    }
  },
  created () {
    // id 
    if (this.$route.query.id) {
      this.articleId = this.$route.query.id
      this.isXiu = true
      this.getarticleById()
    }
  },
  methods: {
    // 
    async putPublished (draft) {
      await this.$http.put(`articles/${this.articleId}?draft=${draft}`, this.queryFormData)
      this.$message.success(!draft ? '' : '')
      this.$router.push('/article')
    },
    // 
    async getarticleById () {
      const { data: { data } } = await this.$http.get(`articles/${this.articleId}`)
      this.queryFormData = data
    },
    // 
    typeChange () {
      this.queryFormData.cover.images = []
    },
    async uploadPublished (draft) {
      console.log('articles?draft=' + draft, this.queryFormData)
      await this.$http.post('articles?draft=' + draft, this.queryFormData)
      this.$message.success(!draft ? '' : '')
      this.$router.push('/article')
    }
  }
}
</script>

<style lang="less" scoped>
.type3 .my-image-container{
  display: inline-block;
  margin-right: 10px;
}
</style>
